// on host page load, sync state
// let shzConnectState = window.localStorage.getItem('amconnect');
chrome.storage.sync.get(['amconnect'], (value) => {
    if (!value) {
        window.localStorage.removeItem('amconnect');
    }
});

// extension called sendMessage with a storage state change. Sync with hosts
chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
    let { eventType, key, data } = request;
    if (eventType == 'storage') {
        if (key == 'amconnect') {
            // it will only event for disconnect/deactivate. Activate flow is handled by
            // shazam.com/signin/complete/confirm which sets localStorage for amconnect
            if (!data) {
                // this will fire window 'storage' event for other processes but not itself
                window.localStorage.removeItem('amconnect');
                // fire to self page
                let event = new CustomEvent('contentscript:storageChange', {
                    detail: { eventType: 'removed', key },
                });
                document.dispatchEvent(event);
            }
        }
    }
    sendResponse({ success: true });
    return true;
});

document.addEventListener('contentscript_host:storageChange', (event) => {
    let { eventType, key, data } = event.detail;
    if (key == 'amconnect') {
        if (eventType == 'removed') {
            chrome.storage.sync.remove(key);
        } else {
            chrome.storage.sync.set(data, () => {});
        }
    }
});
